import React from "react";
import PropTypes from "prop-types";
import { StaticQuery, graphql } from "gatsby";
import SEO from "../components/seo";
import "../assets/styles/index.scss";

/**
 * Query site metas
 */
const query = graphql`
  query SiteMetaQuery {
    site {
      siteMetadata {
        title
        description
        siteUrl
        author
        logo
      }
    }
  }
`;
/**
 *
 * @param {objecg} data from graphQL query
 * @param {object} prop from view
 */
const seoParams = (data, prop) => ({
  title: prop.title || "",
  siteTitle: data.site.siteMetadata.title || "",
  description: prop.description || data.site.siteMetadata.description || "",
  lang: prop.lang || "fr",
  author: prop.author || data.site.siteMetadata.author || "",
  keywords: prop.keywords || [],
  meta: prop.meta || [],
});

/**
 * Layout
 * @param {object} props
 * @returns {class} StaticQuery from Gatsby
 */
function Layout({ children, meta }) {
  return (
    <StaticQuery
      query={query}
      render={(data) => (
        <>
          <SEO {...seoParams(data, meta)} />
          {children}
        </>
      )}
    />
  );
}

Layout.propTypes = {
  children: PropTypes.node,
  meta: PropTypes.object,
};

Layout.defaultProps = {
  children: null,
  meta: {},
};

export default Layout;
