import React from "react";
import { Link } from "gatsby";

/**
 * Check if the route could be managed by Gatsby system
 * @param {string} path
 */
function isNotGatsbyCompliant(path) {
  return path.match(/^(\/\/|www.|http|https|#)/) !== null;
}

/**
 * Link
 * @param {object} props
 * @description
 * use native or Gatsby link according the path
 */
function SmartLink({ to, children, ...extra }) {
  return (
    <>
      {isNotGatsbyCompliant(to) ? (
        <a href={to} {...extra}>
          {children}
        </a>
      ) : (
        <Link to={to} {...extra}>
          {children}
        </Link>
      )}
    </>
  );
}

export default SmartLink;
