import React from "react";
import PropTypes from "prop-types";
import Helmet from "react-helmet";

/**
 * Seo
 * @param {object} props
 * @description
 * add some SEO elements thanks to React-helmet
 */
function SEO({ title, siteTitle, description, lang, author, keywords, meta }) {
  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      title={title || siteTitle}
      titleTemplate={`%s | ${siteTitle}`}
      meta={[
        {
          name: "description",
          content: description,
        },
        {
          property: "og:title",
          content: siteTitle,
        },
        {
          property: "og:description",
          content: description,
        },
        {
          property: "og:type",
          content: "website",
        },
        {
          name: "twitter:card",
          content: "summary",
        },
        {
          name: "twitter:creator",
          content: author,
        },
        {
          name: "twitter:title",
          content: siteTitle,
        },
        {
          name: "twitter:description",
          content: description,
        },
      ]
        .concat(
          keywords.length > 0
            ? {
                name: "keywords",
                content: keywords.join(", "),
              }
            : []
        )
        .concat(meta)}
    />
  );
}

SEO.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  lang: PropTypes.string.isRequired,
  author: PropTypes.string.isRequired,
  keywords: PropTypes.array.isRequired,
  meta: PropTypes.array.isRequired,
};

export default SEO;
